  text-rendering: optimizeLegibility;
  word-break: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: #0000;
  -webkit-text-size-adjust: 100%;
  font-size: 16px;
}

html, body, * {
  font-family: Inter, sans-serif;
}

body {
  color: var(--colors-zekrom);
  background-color: var(--colors-bg-surface);
  height: 100%;
  margin: 0;
  padding: 0;
  display: block;
}

*, :after, :before {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

h2 {
  text-align: center;
  color: var(--colors-pancham);
  font-size: 1.75rem;
  font-weight: 700;
  line-height: 2rem;
}

h3 {
  color: var(--colors-primary-d300);
  align-items: center;
  gap: 6px;
  margin-bottom: 1rem;
  font-size: 1.125rem;
  font-weight: 500;
  line-height: 1.25rem;
  display: inline-flex;
}

h3 .lucide {
  width: 1.25rem;
  height: 1.25rem;
}

.app__header {
  border-bottom: var(--border);
  background-color: var(--colors-bg-surface);
}

.header__container {
  height: var(--header-height);
  align-items: center;
  display: flex;
}

.app__footer {
  border-top: var(--border);
  background-color: var(--colors-bg-surface);
}

.footer__container {
  align-items: center;
  padding: 1rem 0;
  font-size: .875rem;
  display: flex;
}

.app-container {
  max-width: 80rem;
  margin: 0 auto;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.pr-hero {
  height: calc(100vh - var(--header-height));
  background-color: var(--colors-primary-l800);
  padding: 2rem 0;
}

.pr-hero .app-container {
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;
}

.pr-hero__container {
  border: dashed 1px var(--colors-primary-l200);
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  width: 100%;
  padding: 3rem 0;
  display: flex;
}

.pr-hero__left {
  flex-direction: column;
  flex: 1;
  gap: 1.25rem;
  padding-left: 3rem;
  display: flex;
}

.pr-hero__right {
  flex: 1;
  justify-content: center;
  align-items: center;
  margin-right: auto;
  display: flex;
}

.pr-hero__footer {
  flex: 100%;
  gap: 1rem;
  padding-left: 3rem;
  display: flex;
}

.pr-hero__header {
  color: var(--colors-zekrom);
  font-size: 2.5rem;
  font-weight: 700;
  line-height: 3rem;
}

.pr-hero__sub-header {
  color: var(--colors-pancham);
  font-size: 1.25rem;
  font-weight: 500;
  line-height: 1.5rem;
}

.pr-comp {
  background-color: var(--colors-bg-surface);
  padding: 2rem 0 1rem;
}

.pr-comp__header {
  padding: 0 2rem 2rem;
}

@media screen and (width <= 768px) {
