}

@media screen and (width >= 768px) {
  #item_schema__solution .steps-one {
    margin-left: -16.65%;
    margin-right: 16.65%;
  }
}

@media screen and (width <= 768px) {
  #item_schema__solution .steps-two {
    margin-bottom: -100%;
  }
}

@media screen and (width >= 768px) {
  #item_schema__solution .steps-two {
    margin-left: 50%;
    margin-right: -16.65%;
  }
}

@media screen and (width <= 768px) {
  #no__solution .steps-one, #no__solution .steps-two, #no__solution .steps-two-one, #no__solution .steps-two-two {
    padding-bottom: 16.8px;
  }

