.comp__steps-circle {
  border: 4px solid var(--colors-primary);
  background: #fff;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  margin: 15px auto 10px;
  display: block;
}

@media screen and (width <= 768px) {
  .comp__steps-circle {
    float: left;
    margin-left: 10px;
    margin-right: 10px;
  }
}

#no__solution .comp__steps-circle {
  border-color: var(--colors-pecharm);
}

.comp__steps-name, .comp__steps-description {
  margin: 0;
}

