.comp__steps-description {
  color: var(--colors-pecharm);
  margin-top: 4px;
  font-size: .85rem;
  overflow: hidden;
}

@media screen and (width >= 768px) {
  .comp__steps-description {
    text-align: center;
  }
}

.pr-game .app-container {
  padding: 4rem 1.5rem;
}

.pr-game--d2 {
  background-color: var(--colors-bg-surface-second);
}

.pr-game--d2 .app-container {
  border: 1px dashed var(--color-game-d2);
}

.pr-game--cs {
  background-color: #f3ebdc;
}

.pr-game--cs .app-container {
  border-left: 1px dashed var(--color-game-cs);
  border-right: 1px dashed var(--color-game-cs);
  border-bottom: 1px dashed var(--color-game-cs);
}

.pr-game__header {
  align-items: center;
  gap: 8px;
  padding: 0 0 4rem;
  display: inline-flex;
}

.pr-game__middle {
  align-items: flex-start;
  gap: 1rem;
  display: flex;
}

.pr-game__middle-l, .pr-game__middle-r {
  flex: 1;
  padding: 2rem 0;
}

.pr-game__feature-list {
  flex-direction: column;
  gap: 8px;
  margin: 0 0 1rem 1rem;
  padding: 0;
  list-style-type: circle;
  display: flex;
}

.pr-game__actions {
  padding-top: 1rem;
}

.pr-game__client-link img {
  filter: grayscale(75%);
}

.pr-game__client-link:hover img {
  filter: none;
}

.pr-game--in-progress .pr-game--in-progress-title {
  color: var(--colors-primary);
  font-size: 3rem;
  font-weight: 500;
  line-height: 3.5rem;
}

.pr-game--in-progress .pr-game--in-progress-suffix {
  color: var(--colors-pecharm);
  font-size: 1rem;
  line-height: 1.25rem;
}

.pr-features .app-container {
  padding: 4rem 1.5rem;
}

.pr-features__header {
  padding: 0 0 1rem;
}

.pr-features__container {
  align-items: flex-start;
  gap: 10px;
  display: flex;
}

.pr-features__item {
  border: var(--border);
  flex-direction: column;
  flex: 1;
  gap: 12px;
  padding: .5rem;
  display: flex;
}

.pr-features__item .pr-features__item-icon {
  color: var(--colors-primary);
}

.pr-features__item .pr-features__item-icon .lucide {
  width: 24px;
  height: 24px;
}

.pr-features__item .pr-features__item-title {
  margin-bottom: 0;
}

.pr-features__item .pr-features__item-sub-title {
  color: var(--colors-pangoro);
  font-size: .925rem;
  line-height: 1.25rem;
}

.pr-cta {
  background-color: var(--colors-primary-l800);
  padding: 3rem 0;
}

.pr-cta-inner {
  justify-content: center;
  align-items: center;
  display: flex;
}

.app__btn-main {
  cursor: pointer;
  user-select: none;
  color: #fff;
  background: var(--colors-primary);
  border: 1px solid var(--colors-primary);
  border-radius: 2px;
  outline-color: #0000;
  justify-content: center;
  align-items: center;
  gap: .5rem;
  text-decoration: none;
  display: inline-flex;
  position: relative;
  overflow: hidden;
}

.app__btn-lg {
  padding: .625rem .875rem;
  font-size: 1.125rem;
  font-weight: 500;
}

.app__btn-xlg {
  padding: .875rem 1rem;
  font-size: 1.25rem;
  font-weight: 500;
}

.app__gb {
  border: dashed 1px var(--colors-primary-l200);
  flex-direction: column;
  align-items: center;
  width: 52px;
  padding: 2px;
  display: flex;
  position: relative;
}

.app__gb.app__gb--d2 {
  border: dashed 1px var(--color-game-d2);
}

.app__gb.app__gb--cs {
  border: dashed 1px var(--color-game-cs);
}

.app__gb:hover {
  cursor: pointer;
}

.app__gb:hover.app__gb--d2 .app__gb-img-cont {
  background-color: var(--color-game-d2);
}

.app__gb:hover.app__gb--cs .app__gb-img-cont {
  background-color: var(--color-game-cs);
}

.app__gb-status {
  opacity: .7;
  background-color: #3e3e3ecc;
  border-radius: 2px;
  padding: 2px;
  display: inline-flex;
  position: absolute;
  bottom: -4px;
  right: -4px;
}

.app__gb-status .lucide {
  width: 16px;
  height: 16px;
}

.app__gb-status--active {
  color: var(--colors-success400);
}

.app__gb-status--waiting {
  color: var(--colors-warning400);
}

.app__gb-img-cont {
  border-radius: 2px;
  width: 48px;
  height: 48px;
}

.app-game__entity-list {
  flex-wrap: wrap;
  align-items: center;
  gap: 6px;
  display: flex;
}

.app-game__entity {
  color: var(--colors-pangoro);
  border: var(--border);
  padding: .325rem .625rem;
  font-size: .875rem;
  font-weight: 500;
  line-height: 1rem;
}

.app-game__entity:hover {
  cursor: pointer;
  background-color: var(--colors-primary-l300);
  color: #fff;
}

.app-radio-tab input, .app-radio-tab .app-radio-tab__content {
  display: none;
}

.app-radio-tab input[data-app-tab="1"]:checked ~ .app-radio-tab__content[data-app-tab-content="1"], .app-radio-tab input[data-app-tab="2"]:checked ~ .app-radio-tab__content[data-app-tab-content="2"] {
  display: block;
}

.app-radio-tab .app-radio-tab__content {
  padding: 1rem 0;
}

.app-radio-tab label {
  cursor: pointer;
  text-align: center;
  border-bottom: solid 2px var(--colors-pearl);
  user-select: none;
  padding: .5rem 1rem;
  transition: background .25s linear;
  display: inline-block;
}

.app-radio-tab label:hover, .app-radio-tab input:checked + label {
  border-color: var(--colors-primary);
}

.app-sh-container {
  border: var(--border);
  background-color: var(--colors-primary-l800);
  padding: .5rem 1rem;
}

.app-sh {
  color: var(--colors-primary);
  margin: 0;
  font-size: .925rem;
}

.app-sh .sh-pr {
  color: var(--colors-primary-d400);
  font-weight: 500;
}

.app-sh .sh-sec {
  color: var(--colors-pangoro);
  font-size: .875rem;
}

.app-color-primary {
  color: var(--colors-primary);
}

.app-color-pecharm {
  color: var(--colors-pecharm);
}
